.header {
  background-size: cover;
  background-image: url("../images/photos/Cover_photo.jpg");
  background-position: right;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    box-sizing: border-box;
    height: 100vh;
    padding-top: $headerTopPadding;
    padding-bottom: 88px;
 
    @include onTablet {
      padding-top: 34px;
      padding-bottom: 130px;
    }

    @include onDesktop() {
      padding-top: 36px;
      padding-bottom: 200px;
    }
  }

  &__top {
    @include top-actions;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 34px;
    line-height: 36px;
    color: #fff;
    letter-spacing: -1px;
    align-self: center;

    @include onTablet {
      font-size: 64px;
      line-height: 64px;
      letter-spacing: -1.5px;
    }

    @include onDesktop {
      font-size: 80px;
      line-height: 80px;
    }
  }
}
