.move-free {
  padding-bottom: 92px;

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    color: #fff;
    margin: 0 0 32px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__paragraph {
    margin: 0;
    color: #fff;
    font-weight: 500;
    font-size: 22px;
    line-height: 140%;
  }
}
