.page {
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    background-color: #1d1d1d;

    &--last {
      padding-bottom: 120px;
    }
  
    &--first {
      padding-top: 60px;
  
      @include onTablet {
        padding-top: 120px;
      }
    }
  }
}
