.compare {
  padding-bottom: 92px;

  &__title {
    padding-bottom: 48px;

    @include section-title;
    
    @include onTablet {
      font-size: 48px;
    }
  
    @include onDesktop {
      font-size: 64px;
    }
  }

  &__subtitle {
    margin: 0 0 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #fff;
  }

  &__paragraph {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #fff;
  }

  &__photo {
    width: 100%;
    height: 100%;
    margin-bottom: 32px;
    transition: transform 0.3s;
    &:hover {
      transform: scale(1.05);
    }
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #fff;
  }
}
