.menu {
  background-color: #e5e5e5;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding-top: $headerTopPadding;
  }

  &__top {
    @include top-actions;
    margin-bottom: 44px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    color: #1d1d1d;
    font-weight: 600;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
  }

  &__call {
    margin-bottom: 16px;
    width: max-content;
    color: #1d1d1d;
    display: block;
    text-decoration: none;
    font-weight: 600;

    &--phone {
      font-size: 16px;
      line-height: 27px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      padding-bottom: 8px;
      text-transform: uppercase;
      letter-spacing: 2px;
      border-bottom: 1px solid #1d1d1d;
    }
  }
}
