.details {
  padding-bottom: 80px;

  &__title {
    padding-bottom: 48px;

    @include section-title;
    
    @include onTablet {
      font-size: 48px;
      padding-bottom: 64px;
    }
  
    @include onDesktop {
      font-size: 64px;
      padding-bottom: 48px;
    }
  }

  &__feature {
    margin: 0 0 40px;
    @include onTablet {
      margin: 0 0 48px;
    }
  }
}
