.feature {
  align-content: center;
  background-size: cover;
   
  &__photos {
    margin: 0 0 16px;
  }

  &__scale {
    display: inline-block;
    overflow: hidden;
  }

  &__photo {
    height: 100%;
    width: 100%;
    object-fit: cover;
    
    &:hover {
      transition: scale 0.3s;
      scale: 1.05;
    }
    
  }

  &__description--title {
    margin: 0 0 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #fff;
  }

  &__description--paragraph {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #fff;
    margin: 0 0 40px;

    @include onTablet {
      margin: 0 0 48;
    }
  }
}
