.container {
  margin: 0 auto;
  max-width: 320px;
  padding-left: 19px;
  padding-right: 19px;
  
  @include onTablet {
    max-width: 744px;
    padding-left: 72px;
    padding-right: 72px;
  }

  @include onDesktop {
    max-width: 1260px;
    padding-left: 120px;
    padding-right: 120px;
  }
}
