.form-field {
  background: #f8f8fa;
  border-radius: 40px;
  height: 48px;
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 14px 16px;
  color: #292929;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Poppins", sans-serif;

  &::placeholder {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }

  &:hover {
    border: 1px solid #d5d7de;
  }

  &:focus {
    outline: none;
    border: 1px solid #292929;
  }

  &--textarea {
    margin: 0 0 32px;
    height: 148px;
    border-radius: 16px;
    resize: none;
    color: #292929;
  }
}
