.icon {
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &--phone {
    background-image: url("../images/phone.svg");
  }

  &--menu {
    background-image: url("../images/menu.svg");

    &:hover {
      background-image: url("../images/menu_hover.svg");
    }
  }

  &--cross-black {
    background-image: url("../images/cross_black.svg");
  }

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: max-content;
    padding-right: 20px;
    color: #fff;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s;
  }

  &:hover &__tooltip {
    opacity: 1;
    pointer-events: all;
  }
}
