.contact-us {
  &__title {
    @include section-title;
    
    margin: 0 0 48px;
    
    @include onTablet {
      font-size: 48px;
    }
  
    @include onDesktop {
      font-weight: 600;
    }
  }

  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #fff;
  }

  &__info-link {
    display: block;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #fff;

    &:hover {
      color: rgb(169, 185, 185);
    }
  }

  &__field {
    margin: 0 0 16px;
  }

  &__form {
    margin: 0 0 48px;

    @include onTablet {
      margin: 0;
    }
  }
}
